import {Component} from "react";
import PropTypes from "prop-types"
import React from "react";
import Paper from "@material-ui/core/Paper";
import List from "@material-ui/core/List/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import Radio from "@material-ui/core/Radio";
import ListSubheader from "@material-ui/core/ListSubheader/ListSubheader";


class OlimpSettingsSubList extends Component {

    shouldComponentUpdate(nextProps, nextState, nextContext) {
        return (this.state.selected !== nextState.selected ||
            this.props.showError !== nextProps.showError);
    }

    constructor(props) {
        super(props);
        this.state = {
            selected: -1,
        };
    }

    render() {
        let sublistProps = {
            depth: this.props.depth + 1,
            onSelectionChanged: this.props.onSelectionChanged,
            showError: this.props.showError
        };

        return (
            <Paper
                style={{
                    marginLeft: 8,
                    backgroundColor: this.props.showError ?
                        (this.state.selected === -1 ? "#ff9292" : "#fff")
                        : "#fff",
                }}>
                <List
                    subheader={
                        this.props.subhint ?
                            <ListSubheader>
                                {this.props.subhint}
                            </ListSubheader> : null
                    }>
                    {this.props.children.map((elem, index) => {
                        let selected = index === this.state.selected;

                        let toggle = () => {
                            this.setState({
                                selected: index
                            });
                            this.props.onSelectionChanged([...elem.indexes], !elem.children)
                        };

                        return (
                            <div key={index}>
                                <ListItem dense
                                          button
                                          onClick={() => {
                                              toggle();
                                          }}>
                                    <Radio
                                        checked={selected}/>
                                    <ListItemText primary={elem.text} secondary={elem.hint}/>
                                </ListItem>
                                <Collapse in={selected}>
                                    {selected && elem.children ?
                                        <OlimpSettingsSubList
                                            {...sublistProps}
                                            subhint={elem.subhint}
                                            children={elem.children.map((child, childID) => {
                                                let nextChildProps = {
                                                    text: child.title,
                                                    hint: child.hint ? child.hint.help : null,
                                                    subhint: child.hint ? child.hint.instruction : null,
                                                    indexes: [...elem.indexes, childID]
                                                };
                                                if (child.children) {
                                                    return {
                                                        ...nextChildProps,
                                                        children: child.children,
                                                    }
                                                }
                                                return {
                                                    indexes: [...elem.indexes, childID],
                                                    text: `${child.name} - ${child.fullname}`
                                                }
                                            })}/>
                                        : null}
                                </Collapse>
                            </div>
                        )
                    })}
                </List>
            </Paper>
        );
    }
}


class OlimpSettingsList extends Component {


    constructor(props) {
        super(props);
        this.state = {
            selected: null,
        };
    }

    render() {
        return <OlimpSettingsSubList
            children={this.props.children.map((child, id) => {
                return {
                    children: child.children,
                    text: child.title,
                    hint: child.hint ? child.hint.help : null,
                    subhint: child.hint ? child.hint.instruction : null,
                    indexes: [id]
                }
            })}
            showError={this.props.showError}
            depth={0}
            onSelectionChanged={(data, result) => {
                if (this.props.onSelectionChanged)
                    this.props.onSelectionChanged();

                if (result) {
                    if (this.state.selected == null)
                        this.props.onDoneStateChanged(true, data);
                    this.setState({
                        selected: data
                    });
                }
                else {
                    if (this.state.selected)
                        this.props.onDoneStateChanged(false, null);
                    this.setState({
                        selected: null
                    });
                }
            }}
        />
    }
}

OlimpSettingsList.propTypes = {
    children: PropTypes.array.isRequired,
    showError: PropTypes.bool.isRequired,
    onDoneStateChanged: PropTypes.func.isRequired,
    onSelectionChanged: PropTypes.func,
};

export default OlimpSettingsList;