import React, {Component} from 'react';
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel/StepLabel";
import StepContent from "@material-ui/core/StepContent/StepContent";
import List from "@material-ui/core/List/List";
import ListItemText from "@material-ui/core/ListItemText/ListItemText";
import Checkbox from "@material-ui/core/Checkbox/Checkbox";
import ListItem from "@material-ui/core/ListItem/ListItem";
import Button from "@material-ui/core/Button/Button";
import Divider from "@material-ui/core/Divider/Divider";
import OlimpSettingsList from "./OlimpSettingsList";
import ScientificWorkSettings from "./ScientificWorkSettings";
import Popper from "@material-ui/core/Popper";
import Paper from "@material-ui/core/Paper/Paper";
import Fade from "@material-ui/core/Fade/Fade";
import api from "./api/api";

class App extends Component {

    constructor(props) {
        super(props);

        this.api = api;
        this.state = {
            data: null,
            selectedDirections: null,
            step: 0,

            showError: false,
            directionSettingsDoneStates: null,

            scientificWorkSettings: null,

            submitLoading: false
        };
        //
        // fetch("http://195.19.59.194:80")
        //     .then((data) => {return data.json()})
        //     .then((json) => this.onDataLoaded(json["answer"][0]));

        fetch("data/db.json")
            .then((data) => {
                return data.json()
            })
            .then((json) => this.onDataLoaded(json["answer"][0]))
            .catch((reason => {
                console.log(reason);
            }));

        // setTimeout(() => {
        //     let data = sampleData["answer"][0];
        //     this.onDataLoaded(data);
        //
        // }, 1000)
    }

    onDataLoaded = (data) => {
        // console.log(data);
        let selectedDirections = {};
        let directionSettingsDoneStates = {};

        Object.entries(data.children).map(entry => entry[1]).forEach(direction => {
            selectedDirections[direction.title] = false;
            directionSettingsDoneStates[direction.title] = {done: direction.children.length === 0, data: null}
        });

        this.setState({
            selectedDirections: selectedDirections,
            directionSettingsDoneStates: directionSettingsDoneStates,
            data: data
        })
    };

    renderDirectionsList = () => {
        return (
            <List>
                {Object
                    .entries(this.state.data.children)
                    .map(entry => entry[1])
                    .map((direction) => {
                        return (
                            <ListItem dense button key={direction.title} onClick={() => {
                                this.setState({
                                        selectedDirections: {
                                            ...this.state.selectedDirections,
                                            [direction.title]: !this.state.selectedDirections[direction.title]
                                        }
                                    }
                                );
                            }}>
                                <Checkbox
                                    checked={this.state.selectedDirections[direction.title]}
                                    tabIndex={-1}
                                    disableRipple
                                />
                                <ListItemText primary={direction.title} secondary={direction.hint.help}/>
                            </ListItem>
                        );
                    })}
            </List>
        );
    };

    renderDirectionsSettings = () => {
        let directions = this.state.data.children.map(direction => {
            if (this.state.selectedDirections[direction.title]) {
                return direction;
            }
            return null;
        }).filter(direction => direction != null);

        return directions.map(direction => {
            if (direction.children.length === 0) {
                return null;
            }
            return (
                <div key={direction.title} style={{marginTop: 40}}>
                    <Divider/>
                    <Typography variant={"h5"} color={"primary"} style={{padding: 12, paddingBottom: 0}}>
                        {direction.title}
                    </Typography>
                    {/*KOSTYL!!!!!!!*/}

                    {direction.title === "Инженерное дело" && (
                        <ScientificWorkSettings
                            onChange={data => {
                                this.setState({
                                    scientificWorkSettings: data
                                });
                                if (this.state.showError) {
                                    this.setState({
                                        showError: false
                                    });
                                }
                            }}
                            showError={this.state.showError}
                        />
                    )}

                    {/*END KOSTYL!!!!!!!*/}
                    <Typography variant={"subtitle1"} color={"primary"} style={{padding: 10}}>
                        {direction.hint.instruction}
                    </Typography>
                    <OlimpSettingsList
                        children={direction.children}
                        showError={this.state.showError}
                        onDoneStateChanged={(done, data) => {
                            this.setState({
                                directionSettingsDoneStates: {
                                    ...this.state.directionSettingsDoneStates,
                                    [direction.title]: {
                                        done: done,
                                        data: data,
                                    }
                                }
                            });
                        }}
                        onSelectionChanged={() => {
                            if (this.state.showError) {
                                this.setState({
                                    showError: false
                                });
                            }
                        }}
                    />
                </div>
            );
        });
    };

    renderLoading = () => {
        return (
            <div style={{
                position: "relative",
                flex: "1 1 auto",
                display: "flex",
                width: "100%",
                justifyContent: "center",
                alignItems: "center",
                flexDirection: "column"
            }}>
                <Typography variant={"h5"}>
                    Загрузка...
                </Typography>
            </div>
        );
    };

    submitData = () => {
        // let selectedDirections =
        //     Object.keys(this.state.selectedDirections)
        //         .filter((direction) => {
        //             return this.state.selectedDirections[direction];
        //         });

        let directionSettings = Object.keys(this.state.directionSettingsDoneStates)
            .map(direction => {
                return {
                    name: direction,
                    data: this.state.directionSettingsDoneStates[direction].data
                };
            })
            .filter(direction => {
                return direction.data != null;
            })
            .map(direction => {
                let newData = [];

                let tmp = this.state.data.children
                    .filter(child => child.title === direction.name)[0];

                for (let i = 0; i < direction.data.length; i++) {
                    let nowData = direction.data[i];
                    tmp = tmp.children[nowData];
                    if (tmp.title)
                        newData.push(tmp.title);
                    if (tmp.id)
                        newData.push(tmp.id);
                }

                return {
                    name: direction.name,
                    data: newData
                }
            });

        if (this.state.scientificWorkSettings) {
            let data = this.state.scientificWorkSettings;
            if (!data.knownDirector) {
                data = {
                    theme: data.theme,
                    knownDirector: false
                }
            } else {
                let {valid, ...tmp} = data;
                data = tmp;
            }
            directionSettings.push({
                name: "Научрук",
                data: data
            })
        }

        this.setState({
            submitLoading: true
        });
        this.api.requestData("olymps", "POST", directionSettings)
            .then((resp) => {
                window.location.href = `https://olymp.bmstu.ru/?from=olymps&res_code=${resp.res_code}`;
            })
            .catch((error) => {
                this.setState({
                    submitLoading: false
                })
            })
        ;
    };

    render() {
        return (
            <div style={{
                display: "flex",
                flexFlow: "column",
                minHeight: "100vh",
                width: "100%"
            }}>
                <AppBar position="static">
                    <Toolbar>
                        <Typography variant={"h6"} color="inherit" noWrap>
                            Выберите олимпиады
                        </Typography>
                    </Toolbar>
                </AppBar>
                {this.state.data == null ? this.renderLoading() : (
                    <div>
                        <Typography variant={"h4"} color={"primary"} style={{padding: 24, paddingBottom: 0}}>
                            {this.state.data.title}
                        </Typography>
                        <Stepper activeStep={this.state.step} orientation="vertical">
                            <Step>
                                <StepLabel>Выберите направления</StepLabel>
                                <StepContent>
                                    {this.renderDirectionsList()}
                                    <Button
                                        color={"primary"}
                                        variant={"outlined"}
                                        style={{marginLeft: 28}}
                                        onClick={() => {
                                            this.setState({step: 1})
                                        }}
                                        disabled={
                                            !Object.values(this.state.selectedDirections).includes(true)
                                        }
                                    >
                                        Далее
                                    </Button>
                                </StepContent>
                            </Step>
                            <Step>
                                <StepLabel>Заполните данные</StepLabel>
                                <StepContent>
                                    <Button
                                        color={"primary"}
                                        variant={"outlined"}
                                        style={{margin: 8}}
                                        onClick={() => {
                                            this.setState({step: 0, showError: false})
                                        }}
                                    >
                                        Выбрать другие направления
                                    </Button>
                                    <Button
                                        disabled={this.state.submitLoading}
                                        color={"primary"}
                                        variant={"contained"}
                                        style={{margin: 8}}
                                        buttonRef={(r) => {
                                            this.readyButton = r;
                                        }}
                                        onClick={() => {
                                            if (Object.keys(this.state.selectedDirections)
                                                .filter((direction) => {
                                                    return this.state.selectedDirections[direction];
                                                })
                                                .map((direction) => {
                                                    return this.state.directionSettingsDoneStates[direction].done;
                                                })
                                                .includes(false)) {
                                                this.setState({
                                                    showError: true
                                                });
                                                return;
                                            }
                                            if (this.state.selectedDirections["Инженерное дело"]) {
                                                let s = this.state.scientificWorkSettings;
                                                if (!s) {
                                                    this.setState({
                                                        showError: true
                                                    });
                                                    return;
                                                }
                                                if (!s.valid) {
                                                    this.setState({
                                                        showError: true
                                                    });
                                                    return;
                                                }
                                            }
                                            this.submitData()
                                        }}>
                                        Готово
                                    </Button>
                                    <Popper anchorEl={this.readyButton}
                                            open={this.state.showError}
                                            transition>
                                        {({TransitionProps}) => (
                                            <Fade {...TransitionProps} timeout={250}>
                                                <Paper style={{backgroundColor: "#ff9292", padding: 12}}>
                                                    <Typography>
                                                        Все поля должны быть заполнены
                                                    </Typography>
                                                </Paper>
                                            </Fade>
                                        )}
                                    </Popper>
                                    {this.renderDirectionsSettings()}
                                </StepContent>
                            </Step>
                        </Stepper>
                    </div>
                )}
            </div>
        );
    }
}

export default App;
