import {Component} from "react";
import Typography from "@material-ui/core/Typography/Typography";
import TextField from "@material-ui/core/TextField/TextField";
import React from "react";
import * as PropTypes from "prop-types";
import Switch from "@material-ui/core/Switch/Switch";
import Collapse from "@material-ui/core/Collapse/Collapse";

class ScientificWorkSettings extends Component {

    constructor(props) {
        super(props);
        this.state = {
            data: {
                theme: "",
                knownDirector: false,
                fio: "",
                work: "",
                degree: "",
                position: "",
                valid: false
            }
        };
    }

    handleChange = (name, newValue) => {
        let newData = {...this.state.data, [name]: newValue};
        newData.valid = newData.theme !== "" && (!newData.knownDirector || (
            newData.fio !== "" &&
            newData.work !== "" &&
            newData.degree !== "" &&
            newData.position !== ""
        ));
        if (this.props.onChange)
            this.props.onChange(newData);
        this.setState({
            data: newData,
        });
    };

    getSwitch = (name) => {
        return (
            <Switch
                checked={this.state.data[name]}
                onChange={event => {
                    this.handleChange(name, event.target.checked);
                }}
                color="primary"
            />
        );
    };


    getTextField = (label, name) => {
        return (
            <TextField
                key={name}
                error={this.props.showError && this.state.data[name] === ""}
                fullWidth
                label={label}
                value={this.state.data[name]}
                onChange={event => {
                    this.handleChange(name, event.target.value);
                }}
            />
        );
    };

    render() {
        return (
            <div>
                <Typography variant={"subtitle1"} color={"primary"} style={{padding: 10}}>
                    Введите тему научной работы
                </Typography>
                <form noValidate autoComplete="off" style={{paddingLeft: 10, paddingBottom: 30}}>
                    {this.getTextField("Тема научной работы", "theme")}
                </form>
                <Typography variant={"subtitle1"} color={"primary"} style={{padding: 10}}>
                    Введите данные о научном руководителе
                </Typography>
                <div style={{
                    display: "flex",
                    alignItems: "center"
                }}>
                    {this.getSwitch("knownDirector")}
                    <Typography variant={"subtitle2"}>
                        Научный руководитель определен
                    </Typography>
                </div>
                <Collapse in={this.state.data.knownDirector}>
                    <form noValidate autoComplete="off" style={{paddingLeft: 10, paddingBottom: 30}}>
                        {this.getTextField("ФИО", "fio")}
                        {this.getTextField("Место работы", "work")}
                        {this.getTextField("Ученая степень", "degree")}
                        {this.getTextField("Должность", "position")}
                    </form>
                </Collapse>
            </div>
        );
    }
}

ScientificWorkSettings.defaultProps = {
    showError: false,
};

ScientificWorkSettings.propTypes = {
    onChange: PropTypes.func,
    showError: PropTypes.bool,
};

export default ScientificWorkSettings;